*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a{
  color: inherit;
  text-decoration: none;
}

body {
  font-family: 'Source Sans 3', sans-serif;
}
.container {
  min-height: 100vh;
  max-width: 1536px; 
  margin-left: auto;
  margin-right: auto;
  padding-left: 170px;
  padding-right: 170px; 
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  justify-content: center;
  grid-template-areas:
    "header"
    "main"
    "footer";
}