.navbarContainer {
	padding-left: 40px;
  	padding-right: 40px; 
	background-color: black;
	color: white;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: sticky;
	height: 100px;
	top:0;

	
} 

.navbarNavigationLinks {
	display: none;
	justify-content: space-between;
	align-items: center;
	gap:50px;
	
}

.hamburgerMenu {
	background-color: black;
	color: white;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}



.navbarIntials {
	
	flex:1;
}	

@media screen and (min-width: 769px) {
    /* large tablets & small laptops */
    /* .links {
        display: flex;
        justify-content: flex-end;
    } */
	 .navbarSocialLinks {
		display:flex;
	}
	.navbarNavigationLinks {
		display:flex;
	}
    .hamburgerButton {
        display: none;
    }
	.hamburgerMenu {
		display: none;
	}
    
    
}