.name {
	font-size: 6rem;
}

.aboutContainer {
	margin-top: 200px;
}

.aboutContentGrid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px,1fr));
	gap: 100px;
	align-items: center;
}

.image {
	width: 200px;
	height: 200px;
	border-radius: 50%;
	object-fit: cover;
} 

.description {
	margin-top: 100px;
	font-size: 2rem;
	line-height: 1.5;
}


@media screen and (min-width: 769px) {
    .name {
		font-size: 10rem;
	}
	.image {
		width: 300px;
		height: 300px;
	}
    
	.aboutContainer {
		gap:200px;
	}
    
}